import { LinkButton } from '@components/ui/Buttons';
import ErrorReport from '@components/ui/ErrorReport';
import { useRootLayoutData } from '@lib/hooks/useMatchesData';
import { Attribute } from '@lib/utils';
import type { ActionFunctionArgs, LoaderFunctionArgs } from '@remix-run/node';
import { Await, defer, Outlet, useLoaderData } from '@remix-run/react';
import { getOrgRoleByOrgAndRole } from './_manage+/.server/role.queries';
import { requireUser, requireUserAsObject } from '@services/user.server';
import {
  completeItem,
  getChecklistItemsByRole,
  hasCSSRole,
  uncompleteItem,
  denyTask,
} from './.server/tasks.queries';
import React, { Suspense, useState } from 'react';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, jsonWithSuccess } from 'remix-toast';
import { z } from 'zod';
import {
  ArrowDownIcon,
  ArrowUpIcon,
  Bars3BottomRightIcon,
  CheckCircleIcon,
  ClockIcon,
  Cog8ToothIcon,
  FunnelIcon,
  XCircleIcon,
} from '@heroicons/react/24/outline';
import TaskList from '@components/Tasks/TaskList';
import { FilterSortProvider } from '@lib/hooks/useFilterAndSort';

export async function loader({ request, params }: LoaderFunctionArgs) {
  const { roles: userOrgRoles, id: userId } = await requireUserAsObject(
    request,
    { permissions: Attribute.TASKS_READWRITE }
  );
  const { orgRoleId: roleId } = params;
  if (!roleId) throw new Error('roleId is required');

  let orgRole;
  let items;

  if (hasCSSRole(userOrgRoles)) {
    const cssOrgId = userOrgRoles.find(
      (orgRole) => orgRole.abbreviation === 'CSS'
    )?.owningOrgId;
    if (!cssOrgId) throw new Error('CSS owning org id not found.');
    items = getChecklistItemsByRole(roleId, cssOrgId, userId);
    orgRole = await getOrgRoleByOrgAndRole(cssOrgId, roleId);
  } else {
    orgRole = userOrgRoles.find((orgRole) => orgRole.roleId === roleId);
    if (!orgRole)
      throw new Error(`could not find orgrole with role id: ${roleId}`);
    items = getChecklistItemsByRole(roleId, orgRole?.owningOrgId, userId);
  }

  return defer({ items, orgRole, userId });
}

export async function action({ request }: ActionFunctionArgs) {
  const updateTaskSchema = z.object({
    itemId: z.string(),
    postedById: z.string().optional(),
    userId: z.string(),
    content: z.string().optional(),
    _action: z.union([
      z.literal('update'),
      z.literal('incomplete'),
      z.literal('send'),
      z.literal('deny'),
    ]), // with more actions this needs to be z.union([z.literal('update'), z.literal('otherAction')])
  });

  try {
    const approvedUserId = await requireUser(request, {
      options: { redirectOnFailure: false },
      permissions: Attribute.TASKS_READWRITE,
    });

    const { itemId, userId, _action, content, postedById } =
      updateTaskSchema.parse(
        Object.fromEntries((await request.formData()).entries())
      );

    if (_action === 'update') {
      await completeItem(itemId, userId, approvedUserId as string);
      return jsonWithSuccess({ status: 'success' }, 'Task Signed!');
    }
    if (_action === 'incomplete') {
      await uncompleteItem(itemId, userId, approvedUserId as string);
      return jsonWithSuccess({ status: 'success' }, 'Task Unsigned!');
    }
    if (_action === 'deny') {
      await denyTask(itemId, postedById as string, 'Task Denied!');
      return jsonWithError({ status: 'success' }, 'Task Denied!');
    }
  } catch (error) {
    console.error('Error during approval: ', error);
    if (error instanceof Error) {
      if (error instanceof AuthorizationError) {
        return jsonWithError(
          { status: 'error', error: 'Authorization Error.' },
          'User is not authorized.'
        );
      }
      return jsonWithError(
        { status: 'error', error: 'Failed to approve task' },
        'There was an error.'
      );
    }
  }
}

function EditOrgRoleButton(props: {
  orgRole?: {
    name: any;
    description: any;
    id: string;
    abbreviation: any;
    owningOrgId: string;
    roleId: string;
  };
}) {
  if (props.orgRole)
    return (
      <>
        {props.orgRole?.abbreviation !== 'USER' && (
          <LinkButton
            to={{
              pathname: `/tasks/${props.orgRole?.roleId}/edit`,
              searchParams: { orgRole: props.orgRole?.roleId },
            }}
            className="flex items-center gap-1">
            <Cog8ToothIcon className="w-6 h-6" />
            {props.orgRole?.abbreviation === 'CSS'
              ? props.orgRole.abbreviation
              : props.orgRole?.name}{' '}
            Info
          </LinkButton>
        )}
      </>
    );

  return null;
}

const filterLabels = {
  all: { label: 'All', icon: <FunnelIcon className="ml-4 w-5 h-5 mr-2" /> },
  notCompleted: {
    label: 'Not Completed',
    icon: <XCircleIcon className="ml-4 w-5 h-5 mr-2" />,
  },
  completed: {
    label: 'Completed',
    icon: <CheckCircleIcon className="ml-4 w-5 h-5 mr-2" />,
  },
  overdue: {
    label: 'Overdue',
    icon: <ClockIcon className="ml-4 w-5 h-5 mr-2" />,
  },
};

const sortByLabels = {
  default: { label: '', icon: '' },
  newOld: {
    label: 'Newest to Oldest',
    icon: <Bars3BottomRightIcon className="ml-4 w-5 h-5 mr-2" />,
  },
  oldNew: {
    label: 'Oldest to Newest',
    icon: (
      <Bars3BottomRightIcon className="ml-4 w-5 h-5 mr-2 transform scale-y-[-1]" />
    ),
  },
  AtoZ: {
    label: 'A to Z',
    icon: <ArrowDownIcon className="ml-4 w-5 h-5 mr-2" />,
  },
  ZtoA: {
    label: 'Z to A',
    icon: <ArrowUpIcon className="ml-4 w-5 h-5 mr-2" />,
  },
};

export type FilterStatus = keyof typeof filterLabels;
export type SortByStatus = keyof typeof sortByLabels;

const RoleTasks = () => {
  const { items, orgRole: roleInfo } = useLoaderData<typeof loader>();
  const { id: loggedInUserId } = useRootLayoutData('user');

  return (
    <>
      <div className="flex justify-between mb-5">
        <div>
          {roleInfo && <h1 className="text-2xl mb-3">{roleInfo.name}</h1>}
        </div>
        <div>
          <EditOrgRoleButton orgRole={roleInfo} />
        </div>
      </div>

      <Suspense fallback={<p>Loading...</p>} key={roleInfo?.uuid}>
        <Await resolve={items}>
          {(itemsJsonResult) => {
            return (
              <FilterSortProvider
                items={itemsJsonResult}
                labels={{ sortByLabels, filterLabels }}>
                <TaskList loggedInUserId={loggedInUserId} />
              </FilterSortProvider>
            );
          }}
        </Await>
      </Suspense>
      <Outlet />
    </>
  );
};

export const ErrorBoundary = ErrorReport;
export default RoleTasks;
