import React from "react";

export const ChatIcon = ({unreadMessages, MessageComponent}) => {

  return (
    <div className='relative'>
        <div className={`absolute text-white bg-red-500 rounded-xl ${unreadMessages < 10? 'px-[7px]': 'px-[4.5px]'} ${unreadMessages >= 10? unreadMessages >= 100? 'translate-x-[50%]': 'translate-x-[80%]':'translate-x-[100%]'} translate-y-[-20%]`}>{unreadMessages > 0 && unreadMessages}</div>
        <MessageComponent className="size-10"></MessageComponent>
    </div>
  );
};

export default ChatIcon;
