import type {
  FilterStatus,
  SortByStatus,
} from '@routes/_withNav+/_withSideBar+/tasks.$orgRoleId';
import { createContext, useContext, useEffect, useState } from 'react';

export { default as FilterSortMenus } from '@components/Tasks/FilterSortMenus';

function sortItems(items: FilterSortContextValue['filteredItems'], sortState: SortByStatus) {
  let sorted: typeof items = [];

  if (items && items.length > 0) {
    switch ( sortState ) {
      case 'newOld':
        sorted = [
          ...items.sort(
            ( a, b ) =>
              new Date( b.createdAt ).getTime() - new Date( a.createdAt ).getTime()
          ),
        ];
        break;
      case 'oldNew':
        sorted = [
          ...items.sort(
            ( a, b ) =>
              new Date( a.createdAt ).getTime() - new Date( b.createdAt ).getTime()
          ),
        ];
        break;
      case 'AtoZ':
        sorted = [
          ...items.sort( ( a, b ) => a.user.name.localeCompare( b.user.name ) ),
        ];
        break;
      case 'ZtoA':
        sorted = [
          ...items.sort( ( a, b ) => b.user.name.localeCompare( a.user.name ) ),
        ];
        break;
      default:
        sorted = [...items];
        break;
    }
  }

  return sorted;
}

export function useFilterAndSort(
  setLoading: React.Dispatch<React.SetStateAction<boolean>>
) {
  const context = useContext(FilterSortContext);

  const {
    filterState,
    sortState,
    filterLabels,
    sortLabels,
    items,
    filteredItems,
    setFilteredItems,
  } = context ?? {
    items: [],
    filterState: 'all',
    sortState: 'default',
    filterLabels: undefined,
    sortLabels: undefined,
    setFilterState: undefined,
    setSortState: undefined,
    filteredItems: [],
    setFilteredItems: undefined,
  };

  useEffect(() => {
    if (items && setFilteredItems) {
      let filterItems: typeof filteredItems = [];

      switch (filterState) {
        case 'all':
          filterItems = [...items];
          break;
        case 'notCompleted':
          filterItems = items.filter((item) => !item.isComplete);
          break;
        case 'completed':
          filterItems = items.filter((item) => item.isComplete);
          break;
        case 'overdue':
          filterItems = items.filter((item) => item.isOverdue);
          break;
        default:
          break;
      }


      setFilteredItems(sortItems(filterItems, sortState));

      setLoading(false);
    }
  }, [items, filterState, sortState]);

  return {
    filterState: context?.filterState,
    sortState: context?.sortState,
    filteredItems: context?.filteredItems,
    setFilteredItems: context?.setFilteredItems,
    sortLabels,
    filterLabels,
  };
}

export type FilterSortContextValue = {
  filterState: FilterStatus;
  sortState: SortByStatus;
  filterLabels: Record<FilterStatus, { label: string; icon: string | JSX.Element }>;
  sortLabels: Record<SortByStatus, { label: string; icon: string | JSX.Element }>;
  filteredItems: {
    isComplete: boolean;
    isOverdue: boolean;
    createdAt: string;
    user: { name: string };
  }[];
  setFilterState: React.Dispatch<React.SetStateAction<FilterStatus>>;
  setSortState: React.Dispatch<React.SetStateAction<SortByStatus>>;
  items: FilterSortContextValue['filteredItems'];
  setFilteredItems: React.Dispatch<React.SetStateAction<FilterSortContextValue['filteredItems']>>;
};

export const FilterSortContext = createContext<FilterSortContextValue | null>(null);

export function FilterSortProvider({
  items,
  children,
  labels,
}: {
  items: FilterSortContextValue['filteredItems'];
  labels: {
    sortByLabels: FilterSortContextValue['sortLabels'];
    filterLabels: FilterSortContextValue['filterLabels'];
  };
} & React.PropsWithChildren) {
  const { sortByLabels, filterLabels } = labels;
  const [filterStatus, setFilterStatus] = useState<FilterStatus>('all');
  const [sortStatus, setSortStatus] = useState<SortByStatus>('default');
  const [filteredItems, setFilteredItems] = useState<typeof items>([]);

  return (
    <FilterSortContext.Provider
      value={{
        filterState: filterStatus,
        sortState: sortStatus,
        filterLabels,
        sortLabels: sortByLabels,
        items,
        setSortState: setSortStatus,
        setFilterState: setFilterStatus,
        filteredItems,
        setFilteredItems,
      }}>
      {children}
    </FilterSortContext.Provider>
  );
}
