import { Badge, type BadgeProps } from '@components/ui/Badge';
import {
  ChatBubbleOvalLeftEllipsisIcon,
  CheckCircleIcon,
  ArrowDownCircleIcon,
  XCircleIcon,
} from '@heroicons/react/20/solid';
import { UserRank } from '@lib/constants';
import type { TaskTabChecklistItem } from '@lib/types';
import { Link, useFetcher } from '@remix-run/react';
import ChatIcon from './ChatIcon';
import { RichTextView } from '@components/ui/RichText/Viewer';
import { CalendarDaysIcon, CalendarIcon, ArrowUturnLeftIcon } from '@heroicons/react/16/solid';
import { ClipboardDocumentCheckIcon } from '@heroicons/react/24/outline';

export const TaskItem = ({
  item,
  loggedInUserId,
}: {
  item: TaskTabChecklistItem;
  loggedInUserId: string;
}) => {
  const fetcher = useFetcher();
  const createdAt = new Date(item.createdAt);
  const dueDate = new Date(createdAt);
  dueDate.setDate(createdAt.getDate() + 30);

  const today = new Date(Date());
  const thirtyDays = new Date();
  thirtyDays.setDate(today.getDate() + 30);
  const twoWeeks = new Date();
  twoWeeks.setDate(today.getDate() + 14);
  const fiveDays = new Date();
  fiveDays.setDate(today.getDate() + 5);

  let dateBadgeVariant: BadgeProps['variant'] = 'none';

  if (!item.isComplete) {
    if (dueDate < thirtyDays) {
      if (dueDate < twoWeeks) {
        if (dueDate < fiveDays) {
          if (dueDate < today) {
            dateBadgeVariant = 'danger';
          } else dateBadgeVariant = 'warning';
        } else dateBadgeVariant = 'primary';
      }
    }
  }

  return (
    <>
      <tr className="table-row border-b even:bg-zinc-50">
        <td className="text-left pl-5 pr-3 py-2 min-w-[260px] max-w-[260px]">
          <div className="grid gap-y-2">
            <h4 className="font-bold text-lg line-clamp-1 break-all">
              {UserRank[item.user.rank].abb} {item.user.name}
            </h4>
            <div className="text-sm">
              <div className="flex justify-between items-center gap-x-2 px-2 border rounded-xl bg-gray-200">
                <p className="text-center flex-1">
                  {item.user.org.abbreviation}
                </p>
                <span className="text-gray-400">/</span>
                <p className="text-center flex-1">{item.template.type}</p>
              </div>
            </div>
          </div>
        </td>
        <td className="w-[1px] pr-3">
          <div className="border-gray-200 border-x h-[4rem]"></div>
        </td>

        <td className="text-sm align-top py-3 pr-3 min-w-[500px]">
          <div className="flex justify-between">
            <h3 className="line-clamp-1 leading-6 font-bold">
              {item.templateItem.name}
            </h3>
            <div className="inline-flex justify-between items-center gap-x-2 text-sm">
              {!item.isComplete && <> <Badge variant={'none'} className="my-1">
                <CalendarIcon className="size-5 mr-1" />
                Started&nbsp;
                {createdAt.toLocaleString('en-US', {
                  month: 'numeric',
                  day: 'numeric',
                  year: 'numeric',
                })}
              </Badge>
              <Badge variant={dateBadgeVariant} className="my-1">
                <CalendarDaysIcon className="size-5 mr-1" />
                Due&nbsp;
                {dueDate.toLocaleString('en-US', {
                  month: 'numeric',
                  day: 'numeric',
                  year: 'numeric',
                })}
              </Badge></>}
              {item.isComplete && (<>
                <Badge variant="green" className="my-1">
                  <CalendarDaysIcon className="size-5 mr-1" />
                  Signed {new Date(item.dateCompleted).toLocaleString('en-US', {
                    month: 'numeric',
                    day: 'numeric',
                    year: 'numeric',
                  })}
                </Badge></>
              )}
            </div>
          </div>
          <RichTextView className="line-clamp-2">
            {item.templateItem.description}
          </RichTextView>
        </td>
        <td className="w-[1px] pr-3">
          <div className="border-gray-200 border-x h-[4rem]"></div>
        </td>
        <td>
          <div className="flex gap-x-5 justify-between items-center mr-5">
            <div className="text-nowrap flex-grow text-center">
              {item.isComplete && (
                <Badge variant="green">
                  Complete
                </Badge>
              )}
              {!item.isComplete && item.userComplete && (
                <Badge>Ready to Approve</Badge>
              )}
              {!item.isComplete && !item.userComplete && (
                <Badge variant="warning">Awaiting Member</Badge>
              )}
            </div>
            <fetcher.Form method="post" className="">
              <input type="hidden" name="itemId" value={item.id} />
              <input type="hidden" name="userId" value={loggedInUserId} />
              <div className="flex shadow bg-white items-center justify-center justify-items-center rounded-xl divide-x border">
                {!item.isComplete && (
                  <>
                    <button
                      type="submit"
                      name="_action"
                      value="update"
                      className="px-2 py-1 text-green-400 rounded-l-xl hover:text-green-600 hover:cursor-pointer hover:bg-zinc-200"
                      disabled={fetcher.state !== 'idle'}
                    >
                      <CheckCircleIcon className="size-10"></CheckCircleIcon>
                      <span className="sr-only">Approve</span>
                    </button>
                    {item.userComplete && (
                      <Link
                        to={`${item.id}/deny`}
                        className="px-2 py-1 text-red-400 hover:text-red-600 hover:cursor-pointer hover:bg-zinc-200"
                      >
                        <XCircleIcon className="size-10"></XCircleIcon>

                        <span className="sr-only">Deny</span>
                      </Link>
                    )}
                  </>
                )}
                {item.isComplete && (
                  <button
                    type="submit"
                    name="_action"
                    value="incomplete"
                    className="px-2 p-1 text-red-400 rounded-l-xl hover:text-red-600 hover:cursor-pointer h-12 hover:font-bolder hover:bg-zinc-200"
                    disabled={fetcher.state !== 'idle'}
                  >
                    <ArrowUturnLeftIcon className="size-8"></ArrowUturnLeftIcon>
                    <span className="sr-only">Unsign</span>
                  </button>
                )}
                <Link
                  to={`${item.id}/comments`}
                  className="px-2 py-1 hover:text-primary-700 hover:drop-shadow text-primary-500 rounded-r-xl hover:bg-zinc-200"
                >
                  <ChatIcon
                    unreadMessages={item.commentCount}
                    MessageComponent={ChatBubbleOvalLeftEllipsisIcon}
                  />
                  <span className="sr-only">Comments</span>
                </Link>
              </div>
            </fetcher.Form>
          </div>
        </td>
      </tr>
    </>
  );
};

export default TaskItem;
