import { Menu, MenuButton, MenuItem, MenuItems, Transition } from '@headlessui/react';
import { useParams, useSearchParams } from '@remix-run/react';
import type { FilterStatus, SortByStatus } from '@routes/_withNav+/_withSideBar+/tasks.$orgRoleId';
import { Fragment, useContext, useEffect } from 'react';
import {
  AdjustmentsHorizontalIcon,
  FunnelIcon,
} from '@heroicons/react/24/outline';
import { FilterSortContext } from '@lib/hooks/useFilterAndSort';

export default function FilterSortMenus(/* props: {
    filterStatus: FilterStatus;
    sortStatus: SortByStatus;
    setFilterStatus: (status: FilterStatus) => void;
    setSortStatus: (status: SortByStatus) => void;
    filterLabels: Record<FilterStatus, { label: string; icon: string | JSX.Element}>;
    sortLabels: Record<SortByStatus, { label: string; icon: string | JSX.Element}>;
  } */) {
  const context = useContext(FilterSortContext);
  const { orgRoleId } = useParams();
  const [_, setSearchParams] = useSearchParams();

  useEffect(() => {
    if (context)
      context.setFilterState('all');
    // props.setSortStatus('default');
  }, [orgRoleId]);

  if (!context) {
    return 'FilterSortMenu must be in a FilterSortContext Provider.';
  }

  const handleFilterChange = (status: FilterStatus) => {
    // setSearchParams((prev) => {
    //   prev.set('page', '1');
    //   return prev;
    // });
    context.setFilterState(status);
  };  
    
  const handleSortChange = (status: SortByStatus) => {
    // setSearchParams((prev) => {
    //   prev.set('page', '1');
    //   return prev;
    // });
    context.setSortState(status);
  };
  
  return (
    <div className="relative inline-flex gap-x-2 text-left">
      <Menu as="div" className="inline-block text-left">
        <div className="flex gap-3">
          <MenuButton className="inline-flex justify-center w-full rounded-md border border-gray-300 bg-white text-sm font-medium text-gray-700 shadow-sm focus:outline-none focus:ring-2 focus:ring-offset-gray-100">
            <div className="flex text-md px-3 py-1">
              <FunnelIcon className="size-5 mr-2" />
              <span>Filter ({context.filterLabels[context.filterState].label})</span>
            </div>
          </MenuButton>
        </div>

        <Transition
          as={Fragment}
          enter="transition ease-out duration-100"
          enterFrom="transform opacity-0 scale-95"
          enterTo="transform opacity-100 scale-100"
          leave="transition ease-in duration-75"
          leaveFrom="transform opacity-100 scale-100"
          leaveTo="transform opacity-0 scale-95">
          <MenuItems className="absolute left-0 w-44 mt-2 origin-top-right rounded-md bg-white shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none">
            <div className="py-1">
              {Object.keys(context.filterLabels).map((status) => {
                return (
                  <MenuItem
                    key={status}
                    data-selected={status === context.filterState ? true : null}>
                    <button
                      onClick={() => handleFilterChange(status as FilterStatus)}
                      className={`block w-full py-2 text-sm text-gray-700 data-[active]:bg-gray-200 data-[selected]:bg-primary-200`}>
                      <div className="flex items-center">
                        {context.filterLabels[status as FilterStatus].icon}
                        {context.filterLabels[status as FilterStatus].label}
                      </div>
                    </button>
                  </MenuItem>
                );
              })}
            </div>
          </MenuItems>
        </Transition>
      </Menu>
      {context.filteredItems.length > 0 && (
        <Menu as="div" className="inline-block text-left">
          <div className="flex gap-3">
            <MenuButton className="inline-flex justify-center w-full rounded-md border border-gray-300 bg-white text-sm font-medium text-gray-700 shadow-sm focus:outline-none focus:ring-2 focus:ring-offset-gray-100">
              <div className="flex text-md px-3 py-1">
                <AdjustmentsHorizontalIcon className="size-5 mr-2" />
                <span>Sort ({context.sortLabels[context.sortState].label || 'Default'})</span>
              </div>
            </MenuButton>
          </div>

          <Transition
            as={Fragment}
            enter="transition ease-out duration-100"
            enterFrom="transform opacity-0 scale-95"
            enterTo="transform opacity-100 scale-100"
            leave="transition ease-in duration-75"
            leaveFrom="transform opacity-100 scale-100"
            leaveTo="transform opacity-0 scale-95">
            <MenuItems anchor="bottom start" className="absolute left-0 w-44 mt-2 origin-top-right rounded-md bg-white shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none">
              <div className="py-1">
                {Object.keys(context.sortLabels)
                  .filter((key) => key !== 'default')
                  .map((status) => (
                    <MenuItem key={status} data-selected={context.sortState === status ? true : null}>
                      <button
                        onClick={() => handleSortChange(status as SortByStatus)}
                        className={`block w-full py-2 text-sm text-gray-700 data-[active]:bg-gray-200 data-[selected]:bg-primary-200`}>
                        <div className="flex items-center">
                          {context.sortLabels[status as SortByStatus].icon}
                          {context.sortLabels[status as SortByStatus].label}
                        </div>
                      </button>
                    </MenuItem>
                  ))}
              </div>
            </MenuItems>
          </Transition>
        </Menu>
      )}
    </div>
  );
}