import {
  FilterSortMenus,
  useFilterAndSort,
} from '@lib/hooks/useFilterAndSort';
import { useEffect, useState } from 'react';
import TaskItem from './TaskItem';
import { Pagination, usePagination } from '@lib/hooks/usePagination';

export default function TaskList({
  loggedInUserId,
}: {
  loggedInUserId: string;
}) {
  const [showProgressIndicator, setShowProgressIndicator] = useState(true);
  const { filteredItems, filterState, sortState, filterLabels } =
    useFilterAndSort(setShowProgressIndicator);
  const {
    currentItems,
    currentPage,
    totalPages,
    setCurrentPage,
    nextPage,
    prevPage,
    itemPos,
    itemsPerPage,
    setItemsPerPage,
  } = usePagination(filteredItems ?? [], 10);

  useEffect(() => {
    if (currentItems && currentItems.length > 0)
      setCurrentPage(1);
  }, [sortState, filterState]);

  if (showProgressIndicator) {
    return <div className="flex text-lg items-center">
      <svg className="animate-spin -ml-1 mr-2 size-6 text-zinc-900" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
        <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
        <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
      </svg>
      Loading...</div>;
  }

  return (
    <>
      <section className="mb-5">
        <FilterSortMenus />
      </section>
      {currentItems?.length > 0 ? (
        <div>
          <table className="table-auto w-full rounded-t-lg shadow-lg border-collapse">
            <thead>
              <tr className="border-b border-zinc-600 text-zinc-800 uppercase bg-gray-100 dark:bg-gray-700 dark:text-gray-400">
                <th className="text-left px-6 py-3 rounded-tl-lg">Items</th>
                <th></th>
                <th className="text-left px-2 py-3 rounded-tl-lg"></th>
                <th></th>
                <th className="text-center w-72 rounded-tr-lg"></th>
              </tr>
            </thead>
            <tbody>
              {currentItems.map((item) => (
                <TaskItem
                  key={item.id}
                  item={item}
                  loggedInUserId={loggedInUserId}/>
              ))}
            </tbody>
          </table>
          <Pagination
            currentPage={currentPage}
            totalPages={totalPages}
            setCurrentPage={setCurrentPage}
            nextPage={nextPage}
            prevPage={prevPage}
            itemsPerPage={itemsPerPage}
            setItemsPerPage={setItemsPerPage}
            itemPos={itemPos}/>
        </div>
      ) : (
        <p className="text-2xl font-bold">
            No items to show.
        </p>
      )}
    </>
  );
}
